import React, {
  ForwardedRef,
  forwardRef,
  useCallback,
  useRef,
  useState,
} from 'react';
import { css, cx } from 'linaria';
import { BorderStyles } from '../../shared/types/borderTypes';
import { SpacingStyles } from '../../shared/types/spacingTypes';
import { DimensionStyles } from '../../shared/types/dimensionTypes';
import { FlexStyles } from '../../shared/types/flexTypes';
import { DisplayType } from '../../shared/types/displayTypes';
import { ElevationProps } from '../../shared/types/elevationTypes';
import { Box } from '../layout/Box';
import { baseAliases } from '../../shared/aliases/aliases';

const baseStyles = css`
  border: none;
  outline: none;
  width: 100%;
`;

const focusedStyles = css`
  border-color: ${baseAliases.primary};
`;

type TextInputBaseProps = {
  endNode?: React.ReactNode;
  label?: React.ReactNode | 'string';
  onBlur?: React.InputHTMLAttributes<HTMLInputElement>['onBlur'];
  onChange?: React.InputHTMLAttributes<HTMLInputElement>['onChange'];
  onClick?: React.MouseEventHandler;
  onFocus?: React.InputHTMLAttributes<HTMLInputElement>['onFocus'];
  startNode?: React.ReactNode;
  value?: React.InputHTMLAttributes<HTMLInputElement>['value'];
};

type TextInputProps = TextInputBaseProps &
  BorderStyles &
  DimensionStyles &
  FlexStyles &
  SpacingStyles &
  DisplayType &
  ElevationProps &
  React.InputHTMLAttributes<HTMLInputElement>;

export const TextInput = forwardRef(
  (props: TextInputProps, ref: ForwardedRef<HTMLElement>) => {
    const {
      borderColor,
      borderRadius,
      bordered,
      borderedTop,
      borderedBottom,
      borderedLeft,
      borderedRight,
      borderedVertical,
      borderedHorizontal,
      endNode,
      height,
      label,
      onBlur,
      onChange,
      onClick,
      onFocus,
      padding,
      placeholder,
      spacing,
      spacingBottom,
      spacingHorizontal,
      spacingLeft,
      spacingTop,
      spacingVertical,
      startNode,
      width,
    } = props;
    const inputRef = useRef<HTMLInputElement>(null);
    const [focused, setFocused] = useState<boolean>(false);

    const handleNodeClick = useCallback(
      (e: React.MouseEvent<HTMLInputElement>) => {
        setFocused(true);
        inputRef.current?.focus();
      },
      [onChange],
    );

    const handleOnBlur = useCallback(
      (e: React.FocusEvent<HTMLInputElement>) => {
        onBlur?.(e);
        setFocused(false);
      },
      [onBlur],
    );

    const handleOnChange = useCallback(
      (e: React.ChangeEvent<HTMLInputElement>) => {
        onChange?.(e);
      },
      [onChange],
    );

    const handleOnClick = useCallback(
      (e: React.MouseEvent<HTMLInputElement>) => {
        onClick?.(e);
        setFocused(true);
      },
      [onClick],
    );

    const handleOnFocus = useCallback(
      (e: React.FocusEvent<HTMLInputElement>) => {
        onFocus?.(e);
        setFocused(true);
      },
      [onChange],
    );

    return (
      <Box
        flexDirection="column"
        height={height}
        width={width}
        spacing={spacing}
        spacingBottom={spacingBottom}
        spacingHorizontal={spacingHorizontal}
        spacingLeft={spacingLeft}
        spacingTop={spacingTop}
        spacingVertical={spacingVertical}
        onClick={handleNodeClick}
      >
        {label}
        <Box
          alignItems="center"
          borderColor={focused ? 'primary' : borderColor}
          borderRadius={borderRadius}
          bordered={bordered}
          borderedTop={borderedTop}
          borderedBottom={borderedBottom}
          borderedLeft={borderedLeft}
          borderedRight={borderedRight}
          borderedVertical={borderedVertical}
          borderedHorizontal={borderedHorizontal}
          flexDirection="row"
          justifyContent="center"
          spacing={padding}
        >
          {startNode ?? startNode}
          <input
            className={cx(baseStyles, focused && focusedStyles)}
            onClick={handleOnClick}
            onBlur={handleOnBlur}
            onChange={handleOnChange}
            onFocus={handleOnFocus}
            placeholder={placeholder}
            ref={inputRef}
          />
          {endNode ?? endNode}
        </Box>
      </Box>
    );
  },
);
