import React, { ForwardedRef, forwardRef, useCallback } from 'react';
import { BorderStyles } from '../../shared/types/borderTypes';
import { SpacingStyles } from '../../shared/types/spacingTypes';
import { DimensionStyles } from '../../shared/types/dimensionTypes';
import { FlexStyles } from '../../shared/types/flexTypes';
import { DisplayType } from '../../shared/types/displayTypes';
import { ElevationProps } from '../../shared/types/elevationTypes';
import { Box } from '../layout/Box';
import { Spinner } from '../loading/Spinner';
import {
  ButtonVariant,
  getButtonStyles,
} from '../../shared/utils/getButtonStyles';
import { foregroundAliases } from '../../shared/aliases/aliases';
import { css, cx } from 'linaria';

export const fontClass = css`
  font-family: 'Open Sans';
`;

type ButtonBaseProps = {
  endNode?: React.ReactNode;
  loading?: boolean;
  onClick?: React.MouseEventHandler;
  startNode?: React.ReactNode;
  variant?: ButtonVariant;
};

type ButtonProps = ButtonBaseProps &
  BorderStyles &
  DimensionStyles &
  FlexStyles &
  SpacingStyles &
  DisplayType &
  ElevationProps &
  React.InputHTMLAttributes<HTMLInputElement>;

export const Button = forwardRef(
  (props: ButtonProps, ref: ForwardedRef<HTMLElement>) => {
    const {
      endNode,
      height,
      loading,
      onClick,
      padding,
      spacing,
      spacingBottom,
      spacingHorizontal,
      spacingLeft,
      spacingTop,
      spacingVertical,
      startNode,
      variant,
      width,
    } = props;

    const handleOnClick = useCallback(
      (e: React.MouseEvent<HTMLInputElement>) => {
        onClick?.(e);
      },
      [onClick],
    );

    const { color, backgroundColor } = getButtonStyles(variant ?? 'primary');

    return (
      <Box
        flexDirection="column"
        height={height}
        width={width}
        spacing={spacing}
        spacingBottom={spacingBottom}
        spacingHorizontal={spacingHorizontal}
        spacingLeft={spacingLeft}
        spacingTop={spacingTop}
        spacingVertical={spacingVertical}
        onClick={handleOnClick}
      >
        <Box
          alignItems="center"
          background={backgroundColor}
          borderRadius="round"
          flexDirection="row"
          justifyContent="center"
          spacing={padding}
        >
          {loading ? (
            <Spinner color={color} />
          ) : (
            <span>
              {startNode ?? startNode}
              <span
                style={{
                  color: foregroundAliases[color],
                  fontFamily: 'OpenSans',
                }}
                className={cx(fontClass)}
              >
                Sign up
              </span>
              {endNode ?? endNode}
            </span>
          )}
        </Box>
      </Box>
    );
  },
);
